/* http://meyerweb.com/eric/tools/css/reset/ 
 *    v2.0 | 20110126
 *       License: none (public domain)
 *       */
* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Light.woff2") format("woff2");
  font-weight: light;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: medium;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: bold;
}
@font-face {
  font-family: "Guilty Pleasure";
  src: url("../fonts/Guilty_Pleasure_DEMO.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}
.hidden {
  display: none;
  visibility: hidden;
}

.show {
  display: block;
  visibility: visible;
}

:root {
  --gray: #434343;
  --white: #F5F4F3;
  --green: #54906D;
  --green-transparent: rgba(84, 144, 109, 0.5);
  --dark-green: #2C3634;
  --brown: #716963;
  --black: #000000;
  --inner-section-padding: 4.5rem;
  --inner-component-padding: 2.75rem;
  --small-padding: 1.25rem;
  --large-gap: 3.75rem;
  --font-size: 1.25rem;
  --ease: cubic-bezier(.14,.8,.4,1);
  font-family: "Inter";
}

.has-text-align-center {
  text-align: center;
}

.has-text-align-left {
  text-align: left;
}

.has-text-align-right {
  text-align: right;
}

.wp-block-buttons {
  width: fit-content;
  margin: var(--inner-component-padding) auto;
}
.wp-block-buttons .wp-block-button {
  position: relative;
  border: 2px solid var(--gray);
  border-radius: none;
  background-color: transparent;
  cursor: pointer;
}
.wp-block-buttons .wp-block-button .wp-block-button__link {
  position: relative;
  padding: 1.5rem 2.75rem;
  color: var(--gray);
  background-color: transparent;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.3s var(--ease);
  z-index: 2;
}
.wp-block-buttons .wp-block-button::before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: -5px;
  width: 0%;
  height: 100%;
  transform-origin: center left;
  rotate: -3deg;
  background-color: var(--green-transparent);
  transition: all 0.3s var(--ease);
  z-index: 1;
}
.wp-block-buttons .wp-block-button:hover::before, .wp-block-buttons .wp-block-button:focus::before {
  width: calc(100% + 10px);
}

.wp-block-heading,
wp-block-image {
  user-select: none;
}

@media screen and (max-width: 932px) {
  .wp-block-button {
    font-size: 0.875rem;
  }
  .wp-block-button::before {
    width: calc(100% + 10px) !important;
  }
  .wp-block-button .wp-block-button__link {
    padding: 1rem var(--inner-component-padding);
  }
}
html {
  scrollbar-width: thin;
}

body {
  max-width: 2200px;
  margin: 0 auto;
  font-size: var(--font-size);
  font-weight: normal;
  background-color: var(--white);
  color: var(--gray);
}

h1 {
  text-transform: uppercase;
  font-weight: 500;
  line-height: normal;
  font-size: 12.5rem;
  font-family: "Guilty Pleasure";
  color: var(--green-transparent);
  text-align: center;
}

h2,
#header h1 {
  max-width: 1300px;
  margin: 0 auto;
  text-transform: uppercase;
  font-weight: 500;
  line-height: normal;
  font-size: 2.25rem;
  color: var(--green);
  margin-bottom: var(--inner-section-padding);
}

h3 {
  text-transform: uppercase;
  font-weight: 500;
  line-height: normal;
  font-size: 1.5rem;
}

section {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 1rem;
}

p {
  max-width: 1300px;
  margin: 0 auto;
  line-height: 170%;
}

.custom-cursor {
  line-height: 1;
  position: fixed;
  z-index: 999;
  pointer-events: none;
}
.custom-cursor.hovered .cursor-bubble {
  padding: 3rem !important;
}
.custom-cursor .cursor-bubble {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.4s var(--ease);
}
.custom-cursor .cursor-box {
  color: var(--brown);
  position: absolute;
  left: 0.5em;
  right: 0.5em;
  top: 0.5em;
  bottom: 0.5em;
  transition: all 0.4s var(--ease);
}
.custom-cursor .cursor-box .corner {
  width: 0.5em;
  height: 0.5em;
}
.custom-cursor .cursor-box .corner::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 100%;
  background: var(--brown);
}
.custom-cursor .cursor-box .corner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: var(--brown);
}
.custom-cursor .cursor-box .corner.left-top {
  top: 0;
  left: 0;
  position: absolute;
}
.custom-cursor .cursor-box .corner.right-top {
  top: 0;
  right: 0;
  position: absolute;
  transform: rotate(90deg);
}
.custom-cursor .cursor-box .corner.left-bottom {
  bottom: 0;
  left: 0;
  position: absolute;
  transform: rotate(270deg);
}
.custom-cursor .cursor-box .corner.right-bottom {
  bottom: 0;
  right: 0;
  position: absolute;
  transform: rotate(180deg);
}

.contain {
  width: clamp(300px, 100%, 1300px);
  margin-right: auto;
  margin-left: auto;
  padding: 0 1rem;
}

@media screen and (max-width: 932px) {
  :root {
    --inner-section-padding: 2.25rem;
    --inner-component-padding: 2.25rem;
    --small-padding: 0.75rem;
    --large-gap: 2.25rem;
  }
  .custom-cursor {
    display: none;
  }
  body {
    font-size: 1rem;
  }
  h1 {
    font-size: 4rem;
  }
  h2,
  #header h1 {
    font-size: 1.25rem;
  }
  h3 {
    font-size: 1.5rem;
  }
  p {
    font-size: 1rem;
  }
}
.headerParallax {
  position: relative;
  height: 140vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.headerParallax .hero-transition-gradient {
  height: 60vh;
  width: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgb(255, 255, 255) 35%, rgb(255, 255, 255) 75%, rgba(255, 255, 255, 0) 100%);
  position: absolute;
  top: 110vh;
  left: 0;
  z-index: 100;
}
.headerParallax .hero-text {
  color: white;
  font-size: 7rem;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  text-shadow: 0px 0px 100px rgb(0, 0, 0);
  text-align: center;
}
.headerParallax .parallax-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.headerParallax .parallax-layer img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

@media screen and (max-width: 932px) {
  .headerParallax .hero-text {
    font-size: 4rem;
  }
}
nav {
  position: relative;
  z-index: 101;
  background-color: var(--brown);
  border-radius: 3rem;
  color: var(--white);
  padding: 1.5rem;
  width: 80%;
  left: 10%;
  margin-bottom: var(--inner-section-padding);
}
nav ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}
nav ul li:first-child {
  margin-right: auto;
}

.icon-grid {
  display: grid !important;
  margin: var(--inner-section-padding) auto;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, 300px);
  gap: 100px;
  text-align: center;
}
.icon-grid p {
  text-align: center;
}
.icon-grid h3 {
  color: var(--green);
  margin-bottom: var(--small-padding);
}

main.adventure {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 1rem;
  display: grid;
  grid-template-columns: 3fr 4fr;
  gap: var(--large-gap);
}
main.adventure .adventure-image {
  background-color: gray;
  width: 100%;
}
main.adventure .adventure-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
main.adventure h2 {
  color: var(--gray);
}
main.adventure p:not(.meta) {
  margin-bottom: var(--small-padding);
}

@media screen and (max-width: 900px) {
  main.adventure {
    grid-template-columns: 1fr;
    gap: var(--small-gap);
  }
  main.adventure .adventure-image {
    aspect-ratio: 9/16;
    margin-bottom: var(--small-padding);
  }
}
.expedition {
  gap: var(--large-gap);
  margin: calc(var(--large-gap) / 2) 0;
}
.expedition .content {
  padding: 1rem;
}
.expedition figure {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.expedition figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (min-width: 932px) {
  .expedition .wp-block-buttons {
    margin-left: 0;
    margin-bottom: 0;
  }
  .expedition .picture {
    max-width: calc(50% - var(--large-gap) / 2);
  }
  .expedition .content {
    max-width: calc(650px - var(--large-gap) / 2);
    padding-left: 0;
    padding-right: 1rem;
    margin: var(--large-gap) 0;
  }
  .expedition:nth-child(even) {
    flex-direction: row-reverse;
    padding: 0;
  }
  .expedition:nth-child(even) .content {
    padding-right: 0;
    padding-left: 1rem;
  }
}
footer {
  color: var(--white);
  display: grid;
  grid-template-columns: 1fr 1fr;
}
footer .contact,
footer .continents {
  background-color: var(--dark-green);
  padding: var(--large-gap);
  border-top: 2px solid var(--white);
}
footer .continents {
  border-right: 2px solid var(--white);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: var(--inner-component-padding);
}
footer .continents p {
  margin-bottom: 1rem;
}
footer .continents div {
  width: fit-content;
}
footer .continents ul li {
  font-weight: 200;
  font-size: 1rem;
  margin-bottom: 1rem;
}
footer .continents ul a {
  text-decoration: none;
  color: white;
}
footer .contact {
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer .contact .follow {
  font-family: "Guilty Pleasure";
  font-size: 4rem;
  margin-bottom: var(--inner-component-padding);
}
footer .contact ul {
  margin-top: auto;
  margin-bottom: var(--large-gap);
  display: flex;
  gap: var(--inner-component-padding);
}

@media (max-width: 932px) {
  footer {
    grid-template-columns: 1fr;
  }
  footer .continents {
    border-right: none;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }
}